import React, { Component } from "react";

export class Navbar extends Component {
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark mb-3">
        <div className="container-fluid">
          <div className="container">
            <header className="d-flex flex-wrap justify-content-center ">
              <a
                href="/"
                className="d-flex align-items-center mb-3 mb-md-0 me-md-auto text-dark text-decoration-none"
              >
                <svg className="bi me-2" width="40" height="32">
                  
                </svg>
                <span className="fs-4 text-white my-0">Simple header</span>
              </a>

              {/* <ul className="nav nav-pills my-0">
                <li className="nav-item">
                  <a href="/" className="nav-link  my-0" aria-current="page">
                    Home
                  </a>
                </li>
                <li className="nav-item">
                  <a href="/about" className="nav-link my-0">
                   About
                  </a>
                </li> */}
                <div className="nav nav-pills">
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="business">business</button></div>
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="entertainment">entertainment</button></div>
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="general">general</button></div>
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="health">health</button></div>
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="science">science</button></div>
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="sports">sports</button></div>
                <div className="nav-item"><button className="nav-link" onClick={this.props.categories} name="technology">technology</button></div>
                </div>
             
              {/* </ul> */}
            </header>
          </div>
        </div>
      </nav>
    );
  }
}

export default Navbar;
