import React, { Component } from "react";

export class NewItem extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    let { title, desc, imgUrl, newsUrl } = this.props;

    return (
      <div className="col-sm">
        <div className="card" style={{ width: "18rem" }}>
          <img src={imgUrl==null?"https://cleantechnica.com/files/2021/07/Tesla-Model-3-AWD-Japan.png":imgUrl} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title">{title}</h5>
            <p className="card-text">{desc}</p>
            <a href={newsUrl} target="_blank" className="btn btn-success">
              Go somewhere
            </a>
          </div>
        </div>
     
      </div>
    );
  }
}

export default NewItem;
