import React, { Component } from "react";
import NewItem from "./NewItem";
import PropTypes from 'prop-types'

import Spinner from "./Spinner";


export class News extends Component {

 
  constructor(props) {
    super(props);

    this.state = {
      articles:[],
      page:1,
      totalResults:"",
      loading:false

    };
  }
  async componentDidMount(){
    let data= await fetch(`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=9482ba3b19c042dca889099aed112fcc&page=1&pagesize=${this.props.pagesize}`)
    this.setState({loading:true})
    let parseData=await data.json()
    this.setState({loading:false})
    this.setState({articles:parseData.articles,totalResults:parseData.totalResults})

  }
  prevePage=async()=>{
    let data= await fetch(`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=9482ba3b19c042dca889099aed112fcc&page=${this.state.page}&pagesize=${this.props.pagesize}`)
    this.setState({loading:true})
    let parseData=await data.json()
    this.setState({loading:false})
    this.setState({articles:parseData.articles})

this.setState({page:this.state.page -1})}

nextPage=async()=>{
  if(this.state.page+1>Math.ceil(this.state.totalResults/this.props.pagesize)){

  }else{

    let data= await fetch(`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=9482ba3b19c042dca889099aed112fcc&page=${this.state.page}&pagesize=${this.props.pagesize}`)
    this.setState({loading:true})
    let parseData=await data.json()
    this.setState({loading:false})
    this.setState({articles:parseData.articles})
    
    this.setState({page:this.state.page +1})
    
    
  }
}


  render() {
    return (
      <>
      <h1 className="text-center">Top headline News</h1>
      {this.state.loading && <Spinner/>}
      <div className="container d-flex justify-content-center">
        <div className="row">
          {!this.state.loading && this.state.articles.map((val) => {
            return (
              <NewItem
                key={val.url}
                title={val.title}
                desc={val.description}
                imgUrl={val.urlToImage}
                newsUrl={val.url}
              />
            );
          })}
        </div>
      </div>
        <div className="container d-flex justify-content-between my-2">
          <button type="button" disabled={`${this.state.page<=0?"true":""}`} class="btn btn-success" onClick={this.prevePage}>
           prev
          </button>
          <button type="button" disabled={this.state.page+1>Math.ceil(this.state.totalResults/this.props.pagesize)} class="btn btn-success" onClick={ this.nextPage}>
            next
          </button>
        </div>
        </>
    );
  }
}

export default News;
